

const model = (function(){ 

    const fileOperator = {
        
        add(file) {
            file.parent = this;
            return this.children.push(file);
        },

        remove(file) {
            const childIndex = this.children.indexOf(file);
            if (childIndex !== -1)
                this.children.splice(childIndex, 1);
        },

    }
      

    // File Types
    const makeStack  = function(name){
        // children can be stacks and books
        let children = [];

        return{
            name,
            children,
            ...fileOperator,
            makeBook,        
            getType : ()  => "stack",
        }
    }

    const makeBook = function(name){
        let children = []; 

        return{
            name,
            children, 
            ...fileOperator,
            makeNote,
            getType : ()  => "book",
        }
    }

    const makeNote = function(title, details, creationDate){
        return{
            title,
            details,
            creationDate,
            getType : () => "note",
        }
    }
    
    // end

    let defaultStack = makeStack("Stackbook");
    
    return{
        defaultStack,
        makeStack,
    }
    
})();

